//global style
@font-face {
	font-family: GE_Hili_Book;
	src: url(../webfonts/GE_Hili_Book.otf);
}

.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "GE_Hili_Book", sans-serif;

	.main-wrapper {
		background-color: #fff;
		overflow-x: hidden;
		// padding-top: 70px;
		position: relative;
		width: 100%;
	}
}

//global style


//main section
section.main {
	padding: 0 0 50px;
	background-color: #fff;

	.main-title {
		text-align: center;
		// background-color: #482D74;
		padding: 15px 0;

		img {
			width: 550px;
		}
	}

	.content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: 40px;

		.company-box {
			text-align: center;
			padding: 30px;
			transition: all ease 0.3s;
			position: relative;
			cursor: pointer;

			// &::after {
			// 	content: "";
			// 	position: absolute;
			// 	left: 0;
			// 	top: 0;
			// 	width: 100%;
			// 	height: 100%;
			// 	background-color: $main_color;
			// 	z-index: -1;
			// }

			&:hover {
				transform: translate(0, -5px);
				box-shadow: 0 0.1rem 0 0 $main_color,
					0 0.1rem 0 0 $main_color;
					border-color: transparent;
			}

			.image {
				// border: 1px solid $second_color;
				width: 150px;
				height: 150px;
				background-color: $second_color;
				border-radius: 50%;
				margin: auto;
				margin-bottom: 20px;
				display: flex;
				align-items: center;
				justify-content: center;

				img {
					// width: 200px;
					// object-fit: contain;
					// -o-object-fit: contain;
					// height: 130px;
				}
			}

			.title {
				h2 {
					font-size: 20px;
					color: $second_color;
					transition: all ease 0.3s;
					text-transform: capitalize;

					&:hover {
						color: $main_color;
					}
				}
			}
		}
	}
}

//main section


//footer
footer {
	// padding: 50px 0;
	background-color: transparent;

	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.footer-logo {
			width: 200px;
		}
	}

	.footer-companies {
		ul {
			padding: 0;
			margin: 0;

			li {
				list-style: none;
				margin-bottom: 10px;
				display: flex;

				.icon {
					width: 25px;
					height: 25px;
					border-radius: 50%;
					background-color: #fff;
					margin-inline-end: 10px;
					@extend %center;
				}

				img {
					width: 20px;
				}

				&:last-of-type {
					margin-bottom: 0;
				}

				a {
					color: #fff;
					transition: all ease 0.3s;

					&:hover {
						color: $main_color;
					}
				}
			}
		}
	}
}

.copyrights {
	// background-color: $second_color;
	padding: 0 0 15px 0;
	text-align: center;

	p {
		margin-bottom: 0;
		color: $second_color;
		text-transform: capitalize;
	}
}

//footer